 import api from '@/api';
 window.isClick =  false;
 function suc(){alert("");}
 export default {
      lookImg(src,array) {
            console.log("");
     wx.ready(function(){
          if(!window.isClick){
          window.isClick = true;
           wx.previewImage({
               current:src, 
               urls: array
           });
           setTimeout(function () {
            window.isClick = false;         
            }, 700);
        }
         });
      },

        uploadImg(){
           return new Promise((resolve, reject) => {  
         wx.ready(function(){
          console.log("");  
            var images = new Object();
                wx.chooseImage({
                count: 1,
                sourceType: ['album', 'camera'],
                success: function(res) {
                   console.log("");
                   console.log(res);
                    images.localId = res.localIds;
                    var length = images.localId.length;   
                    if (images.localId.length == 0) {
                        alert('');
                        reject();
                        return;
                    }          
                    function upload() {
                        console.log('upload')
                        wx.uploadImage({
                            localId: images.localId[0],
                            isShowProgressTips: 1,
                            success: function(res) {
                              console.log(res);
                                api.ajax('',
                                    {"action":"Art/GetImages","args":{"mediaId":res.serverId,"token":api.token}}
                                    ).then(res=>{
                                    console.log("jaxa");
                                    resolve(res);
                                  }).catch(error=>{
                                     console.log("");
                                     reject(error);
                                     alert("");
                                   })
                            },
                            fail: function (res) {
                               reject(res);
                              console.log("")
                                alert(JSON.stringify(res));
                            }
                        });
                    }
                    upload();
                }
            });
            })      
        });    
       
    },

    share(
      imgurl="../assets/logo.png",
      fn=suc,
      descContent="\r\n\r\n"
      )
       {
        console.log("");
        wx.ready(function(){
           var shareTitle = '';
           var location =  window.location;
           var shareUrl = location+'?&';
       wx.onMenuShareAppMessage({
             title:shareTitle, // 
             desc:descContent, // 
             link:shareUrl, // JS
             imgUrl:imgurl, // 
             type: '', // ,musicvideolinklink
             dataUrl: '', // typemusicvideo
             success:fn,
             cancel: function () { 
             alert("");
             }
             });
      wx.onMenuShareTimeline({
              title:shareTitle,// 
              link:shareUrl, // JS
              imgUrl:imgurl, // 
              success: function () { 
                  alert("")
              },
              cancel: function () { 
                  alert("")
              }
          });
      wx.onMenuShareQQ({
              title:shareTitle,
              desc:descContent,
              link:shareUrl,
              imgUrl:imgurl, 
               success: function () { 
                      alert("");
                  },
                cancel: function () { 
                      alert("");
               }
          });

       wx.onMenuShareWeibo({
                  title:shareTitle, // 
                  desc:descContent, // 
                  link:shareUrl, // 
                  imgUrl:imgurl, // 
                  success: function () { 
                      alert("");
                  },
                  cancel: function () { 
                      alert("");
                  }
              });

       wx.onMenuShareQZone({
               title:shareTitle, // 
                desc:descContent, // 
                link:shareUrl, // 
                imgUrl:imgurl, // 
                success: function () { 
                  alert("");
                },
                cancel: function () { 
                    alert("");
                }
            }); 

         });
    }, 
    adress(){
      console.log("adress");
      var store = window.localStorage;
      wx.ready(function () {
              console.log("")
              wx.getLocation({
                  success: function (res) 
                  {      
                        alert('');
                        console.log(res.latitude);  //
                        console.log(res.longitude);
                        var geocoder = new qq.maps.Geocoder({
                            complete: function (result) {
                                 console.log(result);
                                 var city = result.detail.addressComponents.city;
                                 var district = result.detail.addressComponents.district; 
                                 var address = city+"-"+district;
                         console.log(address);
                                 store.setItem("address",address);
                            }
                      });
                      geocoder.getAddress(new qq.maps.LatLng(res.latitude, res.longitude));
                  },
                  fail: function (res) {
                     console.log('');
                      alert(",");
                  },
                  cancel: function (res) {       
                    console.log('');
                    alert('');
                  }
              });
          });     
  } 
}


